import React from "react";
import { Container, Row, Col } from "react-bootstrap";
// import professionBg from "@styles/assets/images/landing/our-profession-bg.png";
import TalkToUsNew from "@components/common/talk-to-us-new";
import { StaticImage } from "gatsby-plugin-image";
import { Link } from "gatsby";

const AboutUs = (props) => {
  const { db } = props;
  return (
    <Container fluid className="p-0 position-relative">
      <div className="our_profession_sec_bg">
        <StaticImage src="../../../styles/assets/images/landing/our-profession-bg.png" className="focus_img w-100" alt="profession-bg" />
      </div>
      <div className="our_profession_title_sec">
        <h2>
          Our Professionals <span className="about_us_clr_text">Focus</span>
        </h2>
      </div>
      <Row
        className="our_profession_sec"
      >
        <Col
          xxl={5}
          xl={5}
          lg={5}
          md={12}
          className="d-flex align-items-center justify-content-center"
        >
          <div className="our_profession_focus_img">
            <StaticImage
              className="focus_img w-100"
              src="../../../styles/assets/images/recentworks/our-profession-focus.png"
              alt="our-profession-focus"
            />
          </div>
        </Col>
        
        <Col
          xxl={7}
          xl={7}
          lg={7}
          md={12}
          className="d-flex align-items-center justify-content-center"
        >
          <div
            className="about_us_sec_rhs"
            style={{
              maxWidth: "100%",
            }}
          >
            <div className="about_rhs_content_top">
              <p>
                Justtry is your trusted partner in software development,
                offering tailored solutions for startups and enterprises. With a
                dynamic team of over 10+ skilled developers, we specialize in
                crafting MVP software for blockchain ventures.{" "}
              </p><br/>
              <p>
                Our expertise spans a wide range of technologies, including
                Node.js, Angular.js, React.js, Laravel, Mongo DB, and MySQL.
                We've been delivering top-notch IT services globally for more
                than 3+ years.
              </p><br/>
              <p>
                At Justtry, we provide flexible hiring models, including fixed
                projects, hourly projects, dedicated resource hiring, and
                offshore development centers. We're committed to driving your
                business growth through innovative technology solutions.
              </p>
            </div>
            <div
              style={{
                marginTop: "30px",
              }}
              className="decenter_hero_sec_btn d-flex justify-content-center"
            >
              <Link to="/contact-us/" className="about_rhs_btn">
                View Live Demo
              </Link>
              <TalkToUsNew label="Free Consult" isFor="about" db={db} />
            </div>
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default AboutUs;
